import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'rocky-page',
  templateUrl: './rocky.template.html',
  styleUrls: ['./rocky.component.scss']
})
export class RockyComponent implements OnInit {
  public title: String = 'Rocky (mock - click here to toggle layout)';
  public gameId: String = '#0000111';
  public players: any[] = new Array(14);
  public toggleLayout: Boolean = false;

  public winners: any[] = [{
    title: 'Top Day',
    totalWin: 58482,
    chance: 30.9
  },
  {
    title: 'Top Week',
    totalWin: 58482,
    chance: 30.9
  },
  {
    title: 'Top Month',
    totalWin: 58482,
    chance: 30.9
  }];

  constructor() {}

  ngOnInit() {
  }

  public onTitleClick(e: Event) {
    this.toggleLayout = !this.toggleLayout;
  }
}


